.card {
  /* background-color: black; */
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 300px;
  height: 350px;
  border-radius: 20px;
  margin: 20px 20px;
  background-color: rgba(235, 243, 239, 0.877);
  color: black;
  justify-content: center;
  align-items: center;
}

.card-image {
  width: 250px;
  height: 150px;
  border-radius: 10px;
  /* margin-left: 10px; */
  /* border-radius: 20px; */
}

.card-title {
  font-family: "Lobster", cursive;

  /* color: #5f2c3e; */
  font-weight: 900;
  font-size: large;
}

.card-desc {
  font-size: 0.8em;
  font-weight: 700;
  margin-left: 10px;
}

.card-link {
  display: flex;
  text-decoration: none;
  font-size: 1.5em;
  color: rgb(0, 4, 255);
  /* background-color: black; */
  /* width: 100px;
  height: 30px;
  justify-content: center;
  align-items: center;
  padding-bottom: 5px;
  border-radius: 30px; */
}

.card-link:hover {
  transform: scale(1.5);
  color: rgba(0, 0, 255, 0.815);
}
