@import url("https://fonts.googleapis.com/css2?family=Corinthia:wght@700&family=Fuzzy+Bubbles:wght@700&family=Irish+Grover&family=Roboto+Mono:wght@300&family=Smooch&display=swap");

.home {
  display: flex;
  /* width: 100%; */
  /* flex-direction: row-reverse; */
  flex-wrap: wrap-reverse;
  justify-content: center;
  align-items: center;
  margin-left: 50px;
  height: 400px;
  /* padding-bottom: 50px; */
  border-radius: 5px;

  /* background-color: rgba(36, 146, 36, 0.527); */

  margin-bottom: 40px;
}

.intro {
  line-height: 130%;
  /* width: 70%; */
  flex-grow: 1;
  flex-basis: 45%;
  margin-left: 10%;
}

.image {
  /* width: 30%;
   */
  flex: auto;
  float: left;
  transform: translateY(-50px);
}

.image img {
  width: 350px;
  /* clip-path: polygon(20% 20%, 100% 20%, 100% 100%, 0 100%); */
  height: 350px;
  border-radius: 30%;
  /* background: rgba(239, 173, 248, 0.151); */
  /* box-shadow: black 2px 2px 15px; */
}

@media only screen and (min-width: 600px) and (max-width: 768px) {
  .home {
    height: 500px;
  }
  .intro {
    margin-left: 10%;
    align-items: center;
  }
  .image {
    /* margin-right: 50px; */
    /* align-items: center; */
    margin-left: 10%;
  }
  .image img {
    border-radius: 10%;
  }
}

@media only screen and (max-width: 410px) {
  .home {
    height: 500px;
  }
  .intro {
    /* margin-left: 3%; */
    font-size: x-small;
  }
  .image {
    margin-right: 50px;
  }
  .image img {
    width: 250px;
    height: 250px;
    border-radius: 50%;
    /* margin-left: 50px; */
  }
}

@media only screen and (min-width: 410px) and (max-width: 480px) {
  .intro {
    margin-left: 3%;
    font-size: small;
  }
  .image {
    /* margin-right: 50px; */
  }
  .image img {
    width: 250px;
    height: 250px;
    border-radius: 50%;
    margin-left: 10%;
  }
}

@media only screen and (min-width: 777px) and (max-width: 930px) {
  .home {
    height: 400px;
  }
  .image {
    width: 20%;
  }

  .image img {
    width: 100%;
    height: 300px;
    /* border-radius: 20%; */
  }
}

.intro h2 {
  color: rgb(69, 57, 245);
  animation: blink 1.5s infinite;
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.2;
  }
  100% {
    opacity: 1;
  }
}

.intro h4 span {
  font-family: "Fuzzy Bubbles", cursive;
  text-transform: uppercase;
}
