.contact {
  display: flex;
  flex-wrap: wrap;
  margin-top: 40px;
  background-color: black;

  /* margin-bottom: 40px; */
  height: 200px;
  justify-content: space-evenly;
  font-size: 1.5em;
  align-items: center;
  padding-bottom: 30px;
  /* flex-direction: column; */
}

.headings {
  font-size: small;
}

.social-links {
  margin: 20px;
}

.connect .social-links {
  margin: 10px 10px;
}

.get-in-touch,
.connect {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

