body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: rgb(14, 13, 13);
  font-family: "Roboto Mono", monospace;
  color: white;
}

.navigator {
  display: flex;
  margin: 10px 15px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.title {
  color: #5f2c3e;
  /* font-family: "Roboto Mono", monospace; */
  font-family: "Fuzzy Bubbles", cursive;

  font-weight: 900;
  font-size: 50px;
  margin-right: 5px;
  /* margin-left: 8%; */
}

.blinking-line {
  height: 3px;
  background-color: #5f2c3e;
  width: 20px;
  -webkit-transform: translateY(10px);
          transform: translateY(10px);
  -webkit-animation: blink 0.3s infinite;
          animation: blink 0.3s infinite;
}

/* .navigation a {
  text-decoration: none;
  padding: 0 10px;
  color: #2b6777;
  font-weight: 900;
  font-size: large;
} */

@-webkit-keyframes blink {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes blink {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.home {
  display: flex;
  /* width: 100%; */
  /* flex-direction: row-reverse; */
  flex-wrap: wrap-reverse;
  justify-content: center;
  align-items: center;
  margin-left: 50px;
  height: 400px;
  /* padding-bottom: 50px; */
  border-radius: 5px;

  /* background-color: rgba(36, 146, 36, 0.527); */

  margin-bottom: 40px;
}

.intro {
  line-height: 130%;
  /* width: 70%; */
  flex-grow: 1;
  flex-basis: 45%;
  margin-left: 10%;
}

.image {
  /* width: 30%;
   */
  flex: auto;
  float: left;
  -webkit-transform: translateY(-50px);
          transform: translateY(-50px);
}

.image img {
  width: 350px;
  /* clip-path: polygon(20% 20%, 100% 20%, 100% 100%, 0 100%); */
  height: 350px;
  border-radius: 30%;
  /* background: rgba(239, 173, 248, 0.151); */
  /* box-shadow: black 2px 2px 15px; */
}

@media only screen and (min-width: 600px) and (max-width: 768px) {
  .home {
    height: 500px;
  }
  .intro {
    margin-left: 10%;
    align-items: center;
  }
  .image {
    /* margin-right: 50px; */
    /* align-items: center; */
    margin-left: 10%;
  }
  .image img {
    border-radius: 10%;
  }
}

@media only screen and (max-width: 410px) {
  .home {
    height: 500px;
  }
  .intro {
    /* margin-left: 3%; */
    font-size: x-small;
  }
  .image {
    margin-right: 50px;
  }
  .image img {
    width: 250px;
    height: 250px;
    border-radius: 50%;
    /* margin-left: 50px; */
  }
}

@media only screen and (min-width: 410px) and (max-width: 480px) {
  .intro {
    margin-left: 3%;
    font-size: small;
  }
  .image {
    /* margin-right: 50px; */
  }
  .image img {
    width: 250px;
    height: 250px;
    border-radius: 50%;
    margin-left: 10%;
  }
}

@media only screen and (min-width: 777px) and (max-width: 930px) {
  .home {
    height: 400px;
  }
  .image {
    width: 20%;
  }

  .image img {
    width: 100%;
    height: 300px;
    /* border-radius: 20%; */
  }
}

.intro h2 {
  color: rgb(69, 57, 245);
  -webkit-animation: blink 1.5s infinite;
          animation: blink 1.5s infinite;
}

@-webkit-keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.2;
  }
  100% {
    opacity: 1;
  }
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.2;
  }
  100% {
    opacity: 1;
  }
}

.intro h4 span {
  font-family: "Fuzzy Bubbles", cursive;
  text-transform: uppercase;
}

.about-me {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-right: 50px;
  /* background-color: rgba(36, 146, 36, 0.527);
   */
  /* background-color: #fff; */
  border-radius: 5px;
  padding-bottom: 30px;
  margin-bottom: 40px;
}

.about-me-heading {
  font-family: "Lobster", cursive;
  color: #5f2c3e;
  font-weight: 900;
  font-size: xx-large;
}

.about-me-desc {
  margin: 0 10%;
  font-weight: 800;
  font-size: 1.2em;
}

@media only screen and (max-width: 768px) {
  .about-me-desc {
    margin: 0 5%;
    font-weight: 700;
    font-size: 1.1em;
  }
}

.card {
  /* background-color: black; */
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 300px;
  height: 350px;
  border-radius: 20px;
  margin: 20px 20px;
  background-color: rgba(235, 243, 239, 0.877);
  color: black;
  justify-content: center;
  align-items: center;
}

.card-image {
  width: 250px;
  height: 150px;
  border-radius: 10px;
  /* margin-left: 10px; */
  /* border-radius: 20px; */
}

.card-title {
  font-family: "Lobster", cursive;

  /* color: #5f2c3e; */
  font-weight: 900;
  font-size: large;
}

.card-desc {
  font-size: 0.8em;
  font-weight: 700;
  margin-left: 10px;
}

.card-link {
  display: flex;
  text-decoration: none;
  font-size: 1.5em;
  color: rgb(0, 4, 255);
  /* background-color: black; */
  /* width: 100px;
  height: 30px;
  justify-content: center;
  align-items: center;
  padding-bottom: 5px;
  border-radius: 30px; */
}

.card-link:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  color: rgba(0, 0, 255, 0.815);
}

.skill-set {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 50px;
  /* background-color: rgb(252, 249, 85); */
  font-family: "Lobster", cursive;

  border-radius: 5px;
  padding-bottom: 50px;
  margin-bottom: 40px;
}
.skill-title {
  font-family: "Lobster", cursive;

  color: #5f2c3e;
  font-weight: 900;
  font-size: xx-large;
  text-transform: uppercase;
}
.skills {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 30px;
  /* margin-right: ; */
}

.skill-image {
  width: 70px;
  height: 70px;
  margin-left: 20px;
  /* margin-bottom: 40px; */
}

@media only screen and (min-width: 768px) {
  .skill-image {
    width: 150px;
    height: 150px;
  }
}

.project-section {
  /* background-color: rgba(36, 146, 36, 0.527); */
  margin-right: 40px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.projects {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.project-title {
  font-family: "Lobster", cursive;

  color: #5f2c3e;
  font-weight: 900;
  font-size: xx-large;
  text-transform: uppercase;
}

.contact {
  display: flex;
  flex-wrap: wrap;
  margin-top: 40px;
  background-color: black;

  /* margin-bottom: 40px; */
  height: 200px;
  justify-content: space-evenly;
  font-size: 1.5em;
  align-items: center;
  padding-bottom: 30px;
  /* flex-direction: column; */
}

.headings {
  font-size: small;
}

.social-links {
  margin: 20px;
}

.connect .social-links {
  margin: 10px 10px;
}

.get-in-touch,
.connect {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

