.skill-set {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 50px;
  /* background-color: rgb(252, 249, 85); */
  font-family: "Lobster", cursive;

  border-radius: 5px;
  padding-bottom: 50px;
  margin-bottom: 40px;
}
.skill-title {
  font-family: "Lobster", cursive;

  color: #5f2c3e;
  font-weight: 900;
  font-size: xx-large;
  text-transform: uppercase;
}
.skills {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 30px;
  /* margin-right: ; */
}

.skill-image {
  width: 70px;
  height: 70px;
  margin-left: 20px;
  /* margin-bottom: 40px; */
}

@media only screen and (min-width: 768px) {
  .skill-image {
    width: 150px;
    height: 150px;
  }
}
