@import url("https://fonts.googleapis.com/css2?family=Corinthia:wght@700&family=Irish+Grover&family=Roboto+Mono:wght@300&family=Smooch&display=swap");

.about-me {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-right: 50px;
  /* background-color: rgba(36, 146, 36, 0.527);
   */
  /* background-color: #fff; */
  border-radius: 5px;
  padding-bottom: 30px;
  margin-bottom: 40px;
}

.about-me-heading {
  font-family: "Lobster", cursive;
  color: #5f2c3e;
  font-weight: 900;
  font-size: xx-large;
}

.about-me-desc {
  margin: 0 10%;
  font-weight: 800;
  font-size: 1.2em;
}

@media only screen and (max-width: 768px) {
  .about-me-desc {
    margin: 0 5%;
    font-weight: 700;
    font-size: 1.1em;
  }
}
