@import url("https://fonts.googleapis.com/css2?family=Fuzzy+Bubbles:wght@700&family=Irish+Grover&family=Roboto+Mono:wght@300&family=Smooch&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Corinthia:wght@700&family=Fuzzy+Bubbles:wght@400;700&family=Irish+Grover&family=Roboto+Mono:wght@300&family=Smooch&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Irish+Grover&family=Roboto+Mono:wght@300&family=Smooch&display=swap");
.navigator {
  display: flex;
  margin: 10px 15px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.title {
  color: #5f2c3e;
  /* font-family: "Roboto Mono", monospace; */
  font-family: "Fuzzy Bubbles", cursive;

  font-weight: 900;
  font-size: 50px;
  margin-right: 5px;
  /* margin-left: 8%; */
}

.blinking-line {
  height: 3px;
  background-color: #5f2c3e;
  width: 20px;
  transform: translateY(10px);
  animation: blink 0.3s infinite;
}

/* .navigation a {
  text-decoration: none;
  padding: 0 10px;
  color: #2b6777;
  font-weight: 900;
  font-size: large;
} */

@keyframes blink {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}
